<template>
  <div class="berth-box">
    <!-- 高德地图 -->
    <div id="gaodechart"></div>
    <div class="drag" v-draw v-if="isShow">
      <div class="drag-header">
        <i
          class="el-icon-close"
          style="float: right; color: #fff; cursor: pointer"
          @click="isShow = false"
        ></i>
      </div>
      <user-detail @lookover="lookDet"></user-detail>
    </div>
    <!-- 抽屉选择巡检轨迹 -->
    <el-drawer
      title="查看轨迹"
      :visible.sync="drawer"
      direction="rtl"
      size="17%"
      :before-close="handleClose"
    >
      <look-detail></look-detail>
    </el-drawer>
    <!-- 告警列表 -->
    <div class="emergency-box">
      <emergency-list></emergency-list>
    </div>
    <div class="online">
      <online-info></online-info>
    </div>
  </div>
</template>

<script>
import UserDetail from '../device/UserDetail.vue';
import lookDetail from '../device/LookDetail.vue'
import EmergencyList from '../device/EmergencyList.vue'
import OnlineInfo from '../device/OnlineInfo.vue'
export default {
  components: { UserDetail,lookDetail,EmergencyList,OnlineInfo },
  data() {
    return {
      drawer: false,//控制抽屉的显示隐藏
      isShow: false, //控制详情信息的显示隐藏
      // eslint-disable-next-line no-undef
      position: new AMap.LngLat(120.217235, 30.298743),
      testLat: [
        {
          lat: 30.234362,
          lng: 120.203437,
          name: '张三',
        },
        {
          lat: 30.298743,
          lng: 120.217235,
          name: '李四',
        },
        {
          lat: 30.299242,
          lng: 120.022913,
          name: '王五',
        },
      ],
      userImg: require('../../../assets/markerImg/user2.png'),
    };
  },
  mounted() {
    this.initGaode();
  },
  methods: {
    // 子组件控制父组件的抽屉
    lookDet(){
      this.drawer = true
    },
    // 在抽屉关闭前关闭详细情况弹窗
    handleClose(){
      this.isShow = false
      this.drawer = false
    },
    // 初始化高德地图
    initGaode() {
      console.log(this.position);
      // eslint-disable-next-line no-undef
      let map = new AMap.Map('gaodechart', {
        resizeEnable: true, //初始化加载定位
        zoom: 12, //级别
        mapStyle: 'amap://styles/3ae644b7aeec52c4c4e16f400f07bdf0', //设置地图的显示样式
      });
      let markers = [];
      this.testLat.forEach((item) => {
        let markerContent = '';
        markerContent = `
            <div class="custom-content-user"><img src="${this.userImg}"><p>${item.name}</p></div>
            `;
        // eslint-disable-next-line no-undef
        let marker = new AMap.Marker({
          // eslint-disable-next-line no-undef
          position: new AMap.LngLat(item.lng, item.lat),
          // 将 html 传给 content
          content: markerContent,
          // 以 icon 的 [center bottom] 为原点
          // eslint-disable-next-line no-undef
          offset: new AMap.Pixel(0, 0),
        });
        marker.on('click', this.markerClick);
        markers.push(marker);
      });
      map.add(markers);
    },
    markerClick() {
      this.isShow = true;
    },
  },
};
</script>

<style scoped lang='less'>
.berth-box {
  position: relative;
  padding: 1rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  #gaodechart {
    width: 100%;
    height: 100%;
    // background-color: bisque;
    background-color: #090622;
  }
}

::v-deep .custom-content-user {
  width: 15rem;
  display: flex;
  img {
    z-index: 999;
  }
  p {
    margin-left: -1.5rem;
    margin-top: 0.8rem;
    width: 8rem;
    text-align: center;
    color: #fff;
    height: 2.5rem;
    line-height: 2.5rem;
    background-color: #0096ff;
    box-shadow: 0rem 0.3rem 0.6rem rgb(0 0 0 / 16%);
  }
}
.drag {
  position: absolute;
  top: 30%;
  left: 20%;
  width: 300px;
  height: 320px;
  background-color: rgba(0, 0, 0, 0.3);
  // background-color: salmon;
  .drag-header {
    height: 20px;
    font-size: 20px;
    cursor: move;

    // background-color: #fff;
  }
}
.emergency-box{
  width: 25%;
  height: 40%;
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.3);
}
.online{
  width: 25%;
  height: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgba(0, 0, 0, 0.3);
}
</style>